*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: karla, sans-serif;
}

body{
    background-color: rgb(32, 31, 31);
}
header{
    background-image: linear-gradient(to right, #672280, #A626D3);
    display: flex;
    align-items: center;
    padding: 24px;
    color: white;
    justify-content: center;
    flex-wrap: wrap;
    margin-bottom: 32px;
}
header > img{
    margin-right: 16px;
    transform: scaleX(-1);
    width: 42px;
}
.project-number{margin-left: auto;}

.form{
    width: 100%;
    max-width: 600px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr 1fr;
    gap: 26px;
}

.form input{
    text-transform: uppercase;
    border-radius: 8px;
    text-indent: 8px;
    border: 1px solid #D5D4D8;
}
.form button{
    grid-column: span 2;
    border: none;
    border-radius: 8px;
    font-weight: bold;
    font-size: 1.5rem;
    padding: 16px;
    color: white;
    background-image: linear-gradient(to right, #672280,  #A626D3 0%);
    cursor: pointer;
}
.memes{
    padding: 16px 32px;
    width: 100vw;
    display: flex;
    flex-direction: column;
    gap: 32px;
    align-items: center;
}
.memeImage{
    width: 100%;
    max-height: 300px;
}    

.imgContainer{
    color: white;
    position: relative;
}
.imgContainer h1{
    font-family: Impact;
    -webkit-text-stroke: black 1px;
    position: absolute;
    text-align: center;
    width: 100%;
    margin: 4px 0;
    text-transform: uppercase;
}
.topTxt{
    top: 0;
}
.bottomTxt{
    bottom: 0;
}


@media (max-width: 480px){
    .form{
        grid-template-columns: 1fr;
        grid-template-rows: repeat(3, 1fr);
    }
    .form button{
        grid-column: span 1;
    }
}